* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-weight: 300;
}

html, body, #root {
  width: 100%;
  height: auto;
  min-height: 100%;
}

body {
  min-height: 100vh;
  background: #151615;
  font-family: 'Poppins', sans-serif;
}

#root {
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
}

img {
  width: 100%;
}

ul {
  list-style: none;
}

a {
  color: #151615;
}

a.social {
  color: #9e9e9e;
  -webkit-transition: color .45s ease-in-out;
  transition: color .45s ease-in-out;
}

a.social:hover {
  color: #fff;
}
